import './App.css';
import * as XLSX from "xlsx";
// import { Line } from 'react-chartjs-2';
import { Chart, registerables } from 'chart.js';
Chart.register(...registerables);

function App() {
  let questions = [];
  // let responses = [];
  let questionScore = -1;
  let data = [];

  const onChange = (e) => {
    const [file] = e.target.files;
    const reader = new FileReader();

    reader.onload = (evt) => {
      const bstr = evt.target.result;
      const wb = XLSX.read(bstr, { type: "binary" });
      const wsname = wb.SheetNames[0];
      const ws = wb.Sheets[wsname];
      // const data = XLSX.utils.sheet_to_csv(ws, { header: 1 });
      const dataJSON = XLSX.utils.sheet_to_row_object_array(ws);
      data = JSON.stringify(dataJSON);
      // console.log(data);

      // get student names and scores
      const studentScores = getStudentScore(data);
      console.log("studentScores has type " + typeof(studentScores));
      
      const studentScoresSorted = studentScores.sort((a, b) => (a.score < b.score) ? 1 : -1);
      console.log(studentScoresSorted);

      let students = [];
      let scores = [];

      for (let i = 0; i < studentScores.length; i++) {
        students.push(studentScores[i].student);
        scores.push(studentScores[i].score);
      }

      let chartData = {
        labels: students,
        datasets: [{
          backgroundColor: 'rgb(255, 99, 132)',
          borderColor: 'rgb(255, 99, 132)',
          data: scores,
        }]
      };

      let ctx = document.getElementById('myChart').getContext('2d');
      toggleChart("barChart");
      new Chart(ctx, {
        type: 'bar',
        data: chartData,
        options: {
          indexAxis: 'y',
          elements: {
            bar: {
              borderWidth: 2,
            }
          },
          plugins: {
            legend: {
                display: false
            },
          },
          legend: {
            display: false
          },
          tooltips: {
            enabled: false
          }
        },
      });
      //document.getElementById('myChart').canvas.parentNode.style.height = '128px';

      questions = getQuestions(data);
      // responses = getStudentAnswers(data, 1);
    }
    reader.readAsBinaryString(file);
  };
  const questionRow = 2;
  const questionCol = 4;

  function getQuestions(data) {
    console.log("getting questions...");
    let q = [];
    let rows = JSON.parse(data);
    let questionsJSON = rows[questionRow];
    let keys = Object.keys(questionsJSON);
    if (keys.length > questionCol) {
      for (let j = questionCol; j < keys.length; j++) {
        let name = keys[j];
        let value = questionsJSON[name];
        q.push(value);
      }
      console.log("questions");
      console.log(q);
    } else {
      alert("error: pls contact admin - questionsJSON < " + questionCol);
    }    
    return q;
  }

  const studentRow = 4;
  function getStudentAnswers(data, qKey) {
    // console.log("getting answers...");
    // {
    //     "studentName": "Ashley tomanovic",
    //     "response": "A. said ‘Yes’ to doing what God asked of her."
    // }
    // 
    // qKey is the qnum, eg the first q has qKey = 1, etc
    let rows = JSON.parse(data);
    let responses = [];

    // get the score for the question
    let rowValues = rows[rows.length - 2];
    let keys = Object.keys(rowValues);
    let ansKey = keys[questionCol + qKey - 1];
    questionScore = rowValues[ansKey];
    // console.log("question score is " + questionScore);
    // console.log("type " + typeof(questionScore));

    for (let i = studentRow; i < rows.length - 2; i++) {
      let r = {};
      // console.log()
      rowValues = rows[i];
      keys = Object.keys(rowValues);
      let name = keys[0];
      r.studentName = rowValues[name];
      ansKey = keys[questionCol + qKey - 1];
      r.response = rowValues[ansKey];
      responses.push(r);
      // console.log(studentName + ": " + response);
    }

    // console.log(responses);
    return responses;
  }

  function getCorrectAns(responses) {
    // console.log("correct ans");
    let correctAns = "";
    let answers = [];
    let totals = [];
    // let j = 0;
    for (let i = 0; i < responses.length; i++) {
      // answers.push(responses[i].response);
      let j = answers.indexOf(responses[i].response);
      if (j >= 0) {
        totals[j] += 1;
      } else {
        answers.push(responses[i].response);
        totals.push(1);
      }
    }

    let passed = 0;
    // console.log("ques score is " + questionScore);
    if (answers.length === totals.length) {
      for (let i = 0; i < answers.length; i++) {
        if (Math.round(100*totals[i]/responses.length) === questionScore) {
          correctAns = answers[i];
          passed += 1;
        }
      }  
    } else {
      alert("err - answers and totals length dont match, contact admin");
    }

    if (passed !== 1) {
      alert("err - could not identify correct ans, please contact admin");
      correctAns = "";
    }
    return correctAns;
  }

  function getStudentScore(data) {
    // for bar chart
    console.log("processing data...");
    let studentScores = [];
    let rows = JSON.parse(data);
    console.log(rows);
    let studentNameKey = "";
    for (let i = 0; i < rows.length; i++) {
      let p = {};
      let rowValues = rows[i];

      let keys = Object.keys(rowValues);
      for (let j = 0; j < keys.length; j++) {
        let name = keys[j];
        let value = rowValues[name];
        
        if (value === "Class Scoring") {
          studentNameKey = "";
        }

        if (name === studentNameKey & studentNameKey !== "") {
          p.student = value;
        }

        if (value === "Student Name") {
          studentNameKey = name;          
        }

        if (studentNameKey !== "" & name === "__EMPTY_1") {
          p.score = value;
          if (Object.keys(p).length > 1) {
            studentScores.push(p);
          }
          p = {};
        }
      }
    }

    console.log("student names");
    return studentScores;
  }

  let quesNum = 1;
  function prevQuestion() {
    quesNum -= 1;
    if (quesNum < 1) {
      quesNum = 1;
    }
    if (questions.length >= quesNum) {
      generatePieChart(quesNum);
    }
  }
  
  function toggleChart(id) {
    document.getElementById("barChart").style.display = "none";
    document.getElementById("pieChart").style.display = "none";
    document.getElementById("responsesChart").style.display = "none";
    document.getElementById(id).style.display = "block";

    if (id === "pieChart") {
      generatePieChart(quesNum);
    } else if (id === "responsesChart") {
      generateResponsesChart();
    }
  }

  function generateResponsesChart() {
    // get data
    let questions = getQuestions(data);
    let questionLabels = [];
    let scores = [];
    let results = [];
    //studentScores.sort((a, b) => (a.score < b.score) ? 1 : -1);
    for (let i = 0; i < questions.length; i++) {
      let r = {};
      // questionLabels.push(questions[i]);
      r.question = questions[i];
      let responses = getStudentAnswers(data, i+1);
      // console.log("responses");
      // console.log(responses);
      let correctAns = getCorrectAns(responses);
      // console.log("correctans");
      // console.log(correctAns);

      // calculate correct ans
      if (correctAns !== "") {
        let numCorrect = 0;
        for (let j = 0; j < responses.length; j++) {
          if (responses[j].response === correctAns) {
            numCorrect++;
          }
        }
        // scores.push(numCorrect);
        r.score = numCorrect;
      } else {
        // scores.push(0);
        r.score = 0;

      }
      results.push(r);
    }
    
    results.sort((a, b) => (a.score < b.score) ? 1 : -1);
    console.log("results");
    console.log(results);
    for (let i = 0; i < results.length; i++) {
      questionLabels.push(results[i].question);
      scores.push(results[i].score);
    }

    console.log("questionlabels");
    console.log(questionLabels);
    console.log("scores");
    console.log(scores);
    console.log();
    let chartData = {
      labels: questionLabels,
      datasets: [{
        backgroundColor: 'rgb(99, 255, 132)',
        borderColor: 'rgb(99, 255, 132)',
        data: scores,
      }]
    };

    let ctx = document.getElementById('myResponsesChart').getContext('2d');
    new Chart(ctx, {
      type: 'bar',
      data: chartData,
      options: {
        indexAxis: 'y',
        elements: {
          bar: {
            borderWidth: 2,
          }
        },
        plugins: {
          legend: {
              display: false
          },
        },
        legend: {
          display: false
        },
        tooltips: {
          enabled: false
        }
      },
    });

  }
  
  function nextQuestion() {
    if (questions.length > 0) {
      quesNum += 1;
      if (quesNum > questions.length) {
        quesNum = questions.length;
      }
      if (questions.length >= quesNum) {
        generatePieChart(quesNum);
      }
    }    
  }

  let pieChart = new Chart();
  function generatePieChart(quesNum) {
    document.getElementById("questionNumber").innerHTML = "Question " + quesNum;
    document.getElementById("questionText").innerHTML = questions[quesNum-1];
    let responses = getStudentAnswers(data, quesNum);
    let correctAns = getCorrectAns(responses);
    if (correctAns !== "") {
      let labels = [correctAns, "Incorrect"];
      let results = [0, 0];
      for (let i = 0; i < responses.length; i++) {
        if (responses[i].response === correctAns) {
          results[0] += 1;
        } else {
          results[1] += 1;
        }
      }
      
      document.getElementById("pieText").innerHTML = results[0] + "/" + (results[0] + results[1]);
      let chartData = {
        labels: labels,
        datasets: [{
          data: results,
          backgroundColor: [
            'rgb(99, 255, 132)',
            'rgb(255, 99, 132)'
          ]
        }]
      };
  
      let ctx = document.getElementById('myChartPie').getContext('2d');
      pieChart.destroy();
      pieChart = new Chart(ctx, {
        type: 'doughnut',
        data: chartData,
        options: {
          responsive: false,
          maintainAspectRatio: false,
        }
      });
      // document.getElementById('myChartPie').canvas.parentNode.style.height = '128px';

    }
  }

  return (
    <div className="App">
      <input type="file" onChange={onChange} />
      <br></br><br></br>
      <div className="chartBtns">
        <button type="button" className="btn btn-primary" onClick={() => toggleChart("barChart")}>Bar Chart</button>      
        <button type="button" className="btn btn-primary" onClick={() => toggleChart("pieChart")}>Pie Chart</button>
        <button type="button" className="btn btn-primary" onClick={() => toggleChart("responsesChart")}>Responses Chart</button>
      </div>
      
      <div className="charts">
        <div id="barChart" style={{display:"none"}}>
          <h5>Bar Chart</h5>
          <p>The scores of each student.</p>
          <canvas id="myChart"></canvas>
        </div>
        <div id="pieChart" style={{display:"none"}}>
          <h5>Pie Chart</h5>
          <p>The proportion of students who got the correct answer for each question.</p>
          <button type="button" className="btn btn-info" onClick={prevQuestion}>Prev Question</button>      
          <button type="button" className="btn btn-info" onClick={nextQuestion}>Next Question</button>      
          <p id="questionNumber"></p>
          <p id="questionText"></p>
          <div className="pieDiv">
            <canvas id="myChartPie" height="500px"></canvas>
            <p id="pieText"></p>
          </div>
        </div>
        <div id="responsesChart" style={{display:"none"}}>
          <h5>Responses Chart</h5>
          <p>The number of students who returned the correct result for each question.</p>
          <canvas id="myResponsesChart"></canvas>
        </div>
      </div>
    </div>
  );
}

export default App;
